<template>
  <v-card-actions>
    <v-spacer />
    <v-btn
      text
      color="grey"
      @click="closeDialog"
    >
      {{ $t('common.close') }}
    </v-btn>
    <v-btn
      v-if="userApp.id"
      text
      color="primary"
      :loading="loading"
      @click="touch('edit')"
    >
      {{ $t('common.edit') }}
    </v-btn>
    <v-btn
      v-else
      text
      color="primary"
      :loading="loading"
      @click="touch('add')"
    >
      {{ $t('common.create') }}
    </v-btn>
  </v-card-actions>
</template>

<script>
import { required, url }   from 'vuelidate/lib/validators';
import { mapGetters }      from 'vuex';

export default {
  name: 'TiktokUserAppsMainDialogActions',

  data() {
    return {
      loading: false
    };
  },

  validations () {
    const userApp = {
      marketplaceUrl: { required, url },
      appId: { required },
      clickTrackingUrl: { required, url },
      impressionTrackingUrl: { required, url },
    };

    return { userApp };
  },

  computed: {
    ...mapGetters({
      userApp: 'tiktokUserApps/userApp',
      $validation: 'tiktokUserApps/$validation',
    }),
  },

  watch: {
    userApp: {
      deep: true,
      handler() {
        // ПРИ ИЗМЕНЕНИИ ЛЮБОГО СВОЙСТВА СБРАСЫВАТЬ ВАЛИДАЦИЮ
        if (this.$validation) {
          this.$store.dispatch('tiktokUserApps/setValidation', null);
        }
      }
    },
  },

  methods: {
    closeDialog() {
      this.$store.dispatch('tiktokUserApps/closeDialog', 'mainDialog');
    },

    touch(action) {
      this.$v.$touch();

      if (this.$v.$invalid) {
        this.$store.dispatch('tiktokUserApps/setValidation', this.$v);
      } else {
        switch (action) {
        case 'add':
          this.add();
          break;
        case 'edit':
          this.edit();
          break;
        }
      }
    },

    async add() {
      try {
        this.loading = true;
        const data = {
          name: this.userApp.appName,
          app_id: this.userApp.appId,
          package_name: this.userApp.packageName,
          platform: this.userApp.platform,
          download_url: this.userApp.marketplaceUrl,
          icon: this.userApp.appIcon,
          click_tracking_url: this.userApp.clickTrackingUrl,
          impression_tracking_url: this.userApp.impressionTrackingUrl,
        };

        const response = await this.api.post('/tiktok/user-apps', data);
        if (response.status === 201 || response.status === 200) {
          this.$store.dispatch('main/alert', {
            color: 'success',
            message: this.$t('common.success')
          });

          this.$store.dispatch('tiktokUserApps/loadMainTableData', { endpoint: 'tiktok/user-apps' });

          this.closeDialog();
        } else {
          throw new Error('status not 201');
        }
      } catch (error) {
        this.loading = false;
        this.$store.dispatch('main/alert', {
          color: 'error',
          message: this.$t('common.somethingWentWrong')
        });
      }
    },

    async edit() {
      try {
        this.loading = true;
        const data = {
          id: this.userApp.id,
          name: this.userApp.appName,
          app_id: this.userApp.appId,
          package_name: this.userApp.packageName,
          platform: this.userApp.platform,
          download_url: this.userApp.marketplaceUrl,
          icon: this.userApp.appIcon,
          click_tracking_url: this.userApp.clickTrackingUrl,
          impression_tracking_url: this.userApp.impressionTrackingUrl,
        };

        const response = await this.api.put('/tiktok/user-apps/' + this.userApp.id, data);
        if (response.status === 200) {
          this.$store.dispatch('main/alert', {
            color: 'success',
            message: this.$t('common.success')
          });

          this.$store.dispatch('tiktokUserApps/loadMainTableData', { endpoint: 'tiktok/user-apps' });

          this.closeDialog();
        } else {
          throw new Error('status not 200');
        }
      } catch (error) {
        this.loading = false;
        this.$store.dispatch('main/alert', {
          color: 'error',
          message: this.$t('common.somethingWentWrong')
        });
      }
    }
  }
};
</script>