<template>
  <v-dialog
    :value="dialogs.mainDialog"
    persistent
    width="500"
    :transition="false"
    @keydown.esc="closeDialog"
  >
    <v-card>
      <!-- ХЭДЕР ДИАЛОГА -->
      <dialog-header />
      <!-- ХЭДЕР ДИАЛОГА КОНЕЦ-->

      <v-card-text class="pt-5">
        <!-- ССЫЛКА НА ПРИЛОЖЕНИЕ -->
        <v-col v-bind="colsProps">
          <v-text-field
            v-if="userApp.id"
            :value="userApp.marketplaceUrl"
            :label="$t('common.appUrl')"
            outlined
            :disabled="true"
            dense
            hide-details="auto"
            :spellcheck="false"
          />
          <v-text-field
            v-else
            :value="userApp.marketplaceUrl"
            :label="$t('common.appUrl')"
            outlined
            :loading="loadingMarketPlace"
            :disabled="loadingMarketPlace"
            dense
            :error-messages="marketplaceUrlErrors"
            hide-details="auto"
            :spellcheck="false"
            @input="setMarketplaceUrl"
          />
        </v-col>

        <div
          v-if="apiError"
          role="alert"
          class="alert alert-danger"
          style="margin-top: 16px"
        >
          <span>
            <strong>{{ $t('common.getInfoFailed') }}</strong>
            <br>
          </span>
        </div>
        <!-- ССЫЛКА НА ПРИЛОЖЕНИЕ КОНЕЦ -->

        <!-- КАРТОЧКА ПРИЛОЖЕНИЯ -->
        <v-col
          v-if="userApp.appId"
          v-bind="colsProps"
          class="pt-0"
        >
          <v-card
            elevation="0"
          >
            <v-card-text class="d-flex justify-space-between align-center">
              <v-row>
                <v-col
                  cols="4"
                  class="text-end font-weight-bold"
                >
                  App icon
                </v-col>
                <v-col cols="8">
                  <v-img
                    width="64"
                    height="64"
                    :src="`https://lf16-adcdn-sg.ibytedtos.com/obj/${userApp.appIcon}`"
                  />
                </v-col>
                <v-col
                  cols="4"
                  class="text-end font-weight-bold"
                >
                  App name
                </v-col>
                <v-col cols="8">
                  {{ userApp.appName }}
                </v-col>
                <v-col
                  cols="4"
                  class="text-end font-weight-bold"
                >
                  App ID
                </v-col>
                <v-col cols="8">
                  {{ userApp.appId }}
                </v-col>
              </v-row>
            </v-card-text>
          </v-card>
        </v-col>
        <!-- КАРТОЧКА ПРИЛОЖЕНИЯ КОНЕЦ -->

        <!-- ССЫЛКА НА ТРЕКИНГ В СЛУЧАЕ КЛИКА -->
        <v-col
          v-if="userApp.appId"
          v-bind="colsProps"
        >
          <v-text-field
            :value="userApp.clickTrackingUrl"
            :label="$t('common.userApp.clickUrl')"
            outlined
            dense
            :error-messages="hasValidationInvalid('userApp', 'clickTrackingUrl') ? $t('common.required') : null"
            hide-details="auto"
            :spellcheck="false"
            @input="setValue('clickTrackingUrl', $event)"
          />
        </v-col>
        <!-- ССЫЛКА НА ТРЕКИНГ В СЛУЧАЕ КЛИКА КОНЕЦ -->

        <!-- ССЫЛКА НА ТРЕКИНГ В СЛУЧАЕ ПОКАЗА -->
        <v-col
          v-if="userApp.appId" 
          v-bind="colsProps"
        >
          <v-text-field
            :value="userApp.impressionTrackingUrl"
            :label="$t('common.userApp.impressionUrl')"
            outlined
            dense
            :error-messages="hasValidationInvalid('userApp', 'impressionTrackingUrl') ? $t('common.required') : null"
            hide-details="auto"
            :spellcheck="false"
            @input="setValue('impressionTrackingUrl', $event)"
          />
        </v-col>
        <!-- ССЫЛКА НА ТРЕКИНГ В СЛУЧАЕ ПОКАЗА КОНЕЦ -->
      </v-card-text>

      <!-- ДЕЙСТВИЯ ДИАЛОГА -->
      <v-divider />
      <dialog-actions ref="mainDialogActions" />
      <!-- ДЕЙСТВИЯ ДИАЛОГА КОНЕЦ-->
    </v-card>
  </v-dialog>
</template>
<style>

  .alert-danger {
    color: #842029;
    background-color: #f8d7da;
    border-color: #f5c2c7;
  }

  .alert {
    position: relative;
    padding: 1rem 1rem;
    margin-bottom: 1rem;
    border: 1px solid transparent;
    border-radius: 0.25rem;
  }

  /*.cursor-pointer {*/
  /*  cursor: pointer;*/
  /*}*/

</style>
<script>
import validationMixin   from '@/mixins/tiktok/common/validation_mixin';
import { required, url } from 'vuelidate/lib/validators';
import { mapGetters }    from 'vuex';

import DialogActions     from './TiktokUserAppsMainDialogActions.vue';
import DialogHeader      from './TiktokUserAppsMainDialogHeader.vue';

export default {
  name: 'TiktokUserAppsMainDialog',

  components: {
    DialogHeader,
    DialogActions
  },
  
  mixins: [ validationMixin ],

  data() {
    return {
      localMarketplaceUrl: '',
      loadingMarketPlace: false,
      colsProps: {
        cols: '12',
      },
      apiError: false
    };
  },

  validations () {
    const localMarketplaceUrl = { required, url };
    return { localMarketplaceUrl };
  },

  computed: {
    ...mapGetters({
      dialogs: 'tiktokUserApps/dialogs',
      $validation: 'tiktokUserApps/$validation',
      userApp: 'tiktokUserApps/userApp',
    }),

    marketplaceUrlErrors () {
      if (this.hasValidationInvalid('userApp', 'marketplaceUrl')) {
        return this.$t('common.required');
      }
      return null;
    }
  },

  methods: {
    closeDialog() {
      this.$store.dispatch('tiktokUserApps/closeDialog', 'mainDialog');
    },

    setValue(key, value) {
      this.$store.dispatch('tiktokUserApps/setUserAppProperty', { key, value });
    },

    setMarketplaceUrl(value) {
      this.setValue('marketplaceUrl', value);
      this.localMarketplaceUrl = value;

      setTimeout(async () => {
        if (value === this.userApp.marketplaceUrl) {
          this.getAppData();
        }
      }, 500);
    },

    async getAppData() {
      if (this.$v.localMarketplaceUrl.$invalid) return;

      try {
        this.loadingMarketPlace = true;

        this.setValue('appId', null);
        this.setValue('appIcon', null);
        this.setValue('appName', '');
        this.setValue('packageName', '');
        this.setValue('platform', '');

        const response = await this.api(`tiktok/user-app-metadata?download_url=${this.userApp.marketplaceUrl}`);
        const { status, data } = response;
        if (status === 200 && data) {
          this.setValue('appId', data.appId);
          this.setValue('appIcon', data.appIcon);
          this.setValue('appName', data.appName);
          this.setValue('packageName', data.packageName);
          this.setValue('platform', data.platform);
        } else {
          throw new Error('status not 200');
        }
      } catch (error) {
        this.apiError = true;
      } finally {
        this.loadingMarketPlace = false;
      }
    }
  }
};
</script>