<template>
  <div class="d-flex align-center justify-space-between mb-2">
    <!-- УДАЛИТЬ -->
    <v-tooltip bottom>
      <template v-slot:activator="{ on }">
        <v-btn
          small
          :disabled="selected.length === 0"
          color="primary"
          icon
          @click="bulkDelete"
          v-on="on"
        >
          <v-icon :size="12">
            fas fa-trash-alt
          </v-icon>
        </v-btn>
      </template>
      <span>{{ $t('common.delete') }}</span>
    </v-tooltip>
    <div />
    
    <div class="d-flex align-center">
      <!-- КНОПКИ ДЕЙСТВИЙ - СОЗДАТЬ -->
      <v-btn
        small
        color="primary"
        text
        style="min-width: 0;"
        class="mr-2"
        @click="add"
      >
        <v-icon :size="18">
          add
        </v-icon>

        {{ $t('common.add') }}
      </v-btn>
      <!-- КНОПКИ ДЕЙСТВИЙ - СОЗДАТЬ КОНЕЦ -->
    </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'TiktokUserAppsTopbar',

  data() {
    return {
      loading: false
    };
  },

  computed: {
    ...mapGetters({
      selected: 'tiktokUserApps/selected',
    }),
  },

  methods: {
    async add() {
      await this.$store.dispatch('tiktokUserApps/setUserApp');
      this.$store.dispatch('tiktokUserApps/openDialog', 'mainDialog');
    },

    bulkDelete() {
      this.$store.dispatch('tiktokUserApps/openDialog', 'bulkDelete');
    }
  }
};
</script>
