<template>
  <v-card>
    <v-card-text style="padding: 0px 8px 0px 8px">
      <!-- ТАБЛИЦА -->
      <v-data-table
        v-model="selected"
        :items="mainTableData"
        :items-per-page="footerOptions.itemsPerPage"
        :headers="cols"
        fixed-header
        :height="innerHeight - 117"
        show-select
        :loading="mainTableLoading"
        hide-default-footer
      >
        <!-- НАЗВАНИЕ -->
        <template #[`item.name`]="{ item }">
          <div class="d-flex align-center">
            <div class="mr-1">
              <v-img
                width="22"
                height="22"
                :src="`https://lf16-adcdn-sg.ibytedtos.com/obj/${item.icon}`"
              />
            </div>
            <span
              class="cursor-pointer link"
              @click="editApp(item)"
            >{{ item.name }}</span>
          </div>
        </template>
        <!-- НАЗВАНИЕ КОНЕЦ -->

        <!-- ФУТЕР -->
        <template #footer>
          <v-data-footer
            show-first-last-page
            :options="footerOptions"
            :pagination="footerPagination"
            :items-per-page-options="[10, 30, 50, 100]"
            @update:options="updateFooterOptions"
          />
        </template>
      </v-data-table>
    </v-card-text>
  </v-card>
</template>
<style>
  .cursor-pointer {
    cursor: pointer;
  }

  .link {
    color: #2196F3;
    text-decoration-line: underline;
  }
</style>

<script>
import footerDataMixin from '@/mixins/tiktok/common/footer_data';
import { mapFields }   from 'vuex-map-fields';
import { mapGetters }  from 'vuex';
 
export default {
  name: 'TiktokUserAppsMainTable',

  mixins: [ footerDataMixin ],

  computed: {
    ...mapGetters({
      innerHeight: 'main/innerHeight',
      mainTableData: 'tiktokUserApps/mainTableData',
      mainTableLoading: 'tiktokUserApps/mainTableLoading',
      mainTablePagination: 'tiktokUserApps/mainTablePagination',
    }),
    ...mapFields('tiktokUserApps', ['selected']),

    cols() {
      return [
        { text: this.$t('common.name'), value: 'name', sortable: false},
      ];
    },
  },

  methods: {    
    updateFooterOptions({ page, itemsPerPage }) {
      this.$store.dispatch('tiktokUserApps/loadMainTableData', {
        endpoint: 'tiktok/user-apps',
        page,
        itemsPerPage
      });
    },

    async editApp(data) {
      await this.$store.dispatch('tiktokUserApps/setUserApp', data);
      await this.$store.dispatch('tiktokUserApps/openDialog', 'mainDialog');
    },
  }
};
</script>